.cart-page {
  margin-top: 60px;
}

.cart-page h1 {
  padding: 15px !important;
  font-family: "roboto", sans-serif;
  font-weight: normal;
  background-color: rgba(0, 0, 255, 0.072) !important;
}
.cart-page h1 > p {
  font-size: 20px;
  margin-top: 10px;
}
.cart-page img {
  object-fit: contain;
}
.cart-page .product-name {
  font-size: 20px;
  font-weight: 600;
}
.cart-page .product-des {
  margin-top: 0 !important;
  font-size: 10px;
  font-weight: 200;
}
.cart-page .product-price span {
  color: green;
}

.cart-page .card {
  padding: 5px !important;
  height: 150px !important;
  margin-bottom: 5px !important;
}
.cart-page .cart-remove-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.cart-page .cart-summary {
  text-align: center;
  /* margin-top: -140px !important; */
}

@media screen and (max-width: 770px) {
  .cart-page {
    margin-top: 0px;
  }
  .cart-page img {
    display: none;
  }
  .cart-page .product-des {
    display: none;
  }
  .cart-page .cart-remove-btn {
    display: flex;
    align-items: right;
    justify-content: right;
  }
}
@media screen and (max-width: 993px) {
  .cart-page .product-des {
    display: none;
  }
}
